<template>
  <div class="box">
    <div class="searchBox">
      <div align="center"> <img src="../../src/assets/search_logo.png" height="129" width="270" style=""/></div>
      <input type="text" v-model="keyword" placeholder="请输入关键词进行搜索" class="searchInput" style="padding-left: 10px;" @keyup.enter="response">
      <input type="button"  @click="response" value="搜索" class="searchButton">
    </div>
  </div>
</template>

<script>
    export default {
      data () {
        return {
          keyword : ''
        }
      },
      methods: {
        response() {
          // let url = 'http://s.tanruo.com:9972/#/search/' + this.keyword;
          let url = 'http://192.168.88.114:8003/#/search/' + this.keyword;
          window.open(url, '_blank')
        },

      },

    }
</script>

<style scoped>
  .box{
    margin: 0 auto;
    padding-top: 80px;
    height: 50px;
    width: 100%;
  }
  .searchBox{
    margin: 0 auto;
    width: 600px;
    position: relative;
  }
  .searchInput{
    display: inline-block;
    width: 85%;
    height: 38px;
    border: 1px solid #cccccc;
    float: left;
    box-sizing: border-box;
    -moz-box-sizing:border-box; /* Firefox */
    -webkit-box-sizing:border-box; /* Safari */
    border-bottom-left-radius: 5px;
    border-top-left-radius: 5px;
  }
  .searchButton{
    display: inline-block;
    width: 15%;
    height: 38px;
    line-height: 40px;
    float: left;
    background-color: #00a0e9;
    font-size: 16px;
    cursor: pointer;
    border-bottom-right-radius: 5px;
    border-top-right-radius: 5px;
    border: none;
    color: #fff;
  }

</style>



